
* {
  padding: 0;
  margin: 0;
}
i {
  display: inline-block;
  position: absolute;
}
i:focus{
  outline: none;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  position: relative;
}
.pic-box {
  height: 300px;
  float: left;
}
.title {
  text-align: center;
  background: #727f80;
  color: #fff;
  height: 100px;
  line-height: 100px;
  margin-bottom: 20px;
}
.container {
  margin: 0 auto;
  width: 1000px;
}
.style-box {
  font-family: consolas,monaco,"Andale Mono",monospace;
  margin-top: 50px;
  text-align: center;
  font-size: 16px;
  background: #f6f6f6;
  padding: 20px 0;
}
.style-box p {
  line-height: 40px;
}
.clearfix{*+height:1%;
}
.clearfix:after{content:".";display:block;height:0;clear:both;visibility:hidden
}
.scroll-box {
  font-family: "Open Sans",arial,x-locale-body,sans-serif;
  position: relative;
  float: right;
  line-height: 28px;
  margin-right: 80px;
}
.scroll-line {
  height: 40px;
}
.scroll-name {
  position: absolute;
  text-align: right;
  left: -230px;
  display: inline-block;
  width: 130px;
}
.scroll-val {
  position: absolute;
  left: -85px;
  color: #0d91bc;
}
.matrix-one {
  display: inline-block;
  -webkit-transition: all .5s;
  transition: all .5s;
  position:relative;
  height: 120px;
  margin: 40px 20px;
}
.matrix-title {
  position: absolute;
  bottom: -40px;
  width: 100%;
  text-align: center;
  margin-right: 10px;
}
.tip {
  background: #727f80;
  color: #fff;
  text-align: center;
  line-height: 60px;
}
@media (max-width: 1000px) {
.container{
    width: 100%;
}
}
@media (max-width: 768px) {
.scroll-box {
    float: none;
}
.pic-box {
    float: none;
}
.scroll-name{
    left: 0;
}
.scroll-val {
    left: 150px;
}
.scroll {
    margin-left: 240px;
    vertical-align: 12px;
}
body,.style-box,.tip {
    font-size: 14px;
}
}



