
.scroll[data-v-2d2e691a] {
  position: relative;
  margin-top: 7px;
  display: inline-block;
}
.scroll-bar[data-v-2d2e691a]{
  margin-left: 10px;
  width: 200px;
  height: 12px;
  border-radius: 6px;
  background: #ccc;
  cursor: pointer;
}
.scroll-ball[data-v-2d2e691a] {
  top: -4px;
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 10px;
  background: #e96900;
  cursor: pointer;
}
.icon-plus[data-v-2d2e691a] {
  cursor: pointer;
  right: -40px;
  top: -10px;
  width: 32px;
  height: 32px;
  background: url('../assets/scroll/plus.png') no-repeat;
}
.icon-plus[data-v-2d2e691a]:hover, .icon-plus[data-v-2d2e691a]:active {
  background-image: url(../assets/scroll/plus_fill.png);
}
.icon-minus[data-v-2d2e691a] {
  cursor: pointer;
  left: -30px;
  top: -10px;
  width: 32px;
  height: 32px;
  background: url('../assets/scroll/minus.png') no-repeat;
}
.icon-minus[data-v-2d2e691a]:hover, .icon-minus[data-v-2d2e691a]:active {
  background-image: url(../assets/scroll/minus_fill.png);
}
.icon-close[data-v-2d2e691a] {
  cursor: pointer;
  right: -70px;
  top: -8px;
  width: 28px;
  height: 28px;
  background: url('../assets/scroll/close.png') no-repeat;
}
.icon-close[data-v-2d2e691a]:hover, .icon-close[data-v-2d2e691a]:active {
  background-image: url(../assets/scroll/close_fill.png);
}
@media (max-width: 768px) {
.scroll-bar[data-v-2d2e691a],.scroll-ball[data-v-2d2e691a] {
    display: none;
}
.scroll-ball[data-v-2d2e691a] {
    float: none;
}
}



